import Image from "next/image"

interface LogoProps {
  className?: string
  size?: string
  width?: number
  height?: number
}

const StrictlyWoofsLogo = ({ className = "", size = "h-12", width = 200, height = 50 }: LogoProps) => {
  // Add width class for larger sizes to prevent constraint issues
  const isLargeSize = size.includes("h-20") || size.includes("h-32") || size.includes("h-40") || size.includes("h-48") || size.includes("h-64") || size.includes("h-80")
  const sizeClasses = isLargeSize ? `${size} w-auto max-w-none` : size

  return (
    <div className={`${sizeClasses} ${className} relative`}>
      <Image
        src="/StrictlyWoof_lwhite_SVG.svg"
        alt="Strictly Woofs"
        width={width}
        height={height}
        className="w-full h-full object-contain"
        priority
      />
    </div>
  )
}

export default StrictlyWoofsLogo
